/** @format */

.loader-text {
	position: absolute;
	top: 60%;
	left: 50%;
	transform: translate(-50%, -50%);
	color: rgb(18, 134, 72);
	font-size: 12px;
}

.dashboard-tab-loader-container {
	position: absolute;
	text-align: center;
	background: rgba(0, 0, 0, 0.02);
	height: 100%;
	width: 100%;
}

.lds-ripple {
	position: absolute;
	width: 64px;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	height: 64px;
}

.lds-ripple div {
	position: absolute;
	border: 4px solid #1eba68;
	opacity: 1;
	border-radius: 50%;
	animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.lds-ripple div:nth-child(2) {
	animation-delay: -0.5s;
}
@keyframes lds-ripple {
	0% {
		top: 28px;
		left: 28px;
		width: 0;
		height: 0;
		opacity: 1;
	}
	100% {
		top: -1px;
		left: -1px;
		width: 58px;
		height: 58px;
		opacity: 0;
	}
}
